@import '../../../anchors/pathway-card';

.home__pathway {
    margin: $measure--medium1 0;

    @include media('<tablet') {
        .triangle__block {
            display: none;
        }
    }

    @include media('<small') {
        .pathway {
            @include default-margin();
        }
    }

    @include media('>=small') {
        .pathway {
            @include standard-box();
        }
    }

    @include media('>=large') {
        --l: 12vw;
    }

    // Generic
    .triangle__block {
        width: $measure--medium11 + $measure--quarter;
        height: $measure--medium11 + $measure--quarter;
    }

    .triangle__block--left {
        margin-bottom: -$measure--medium11 - $measure--quarter;
    }

    .triangle__block--right {
        right: 0;
        position: absolute;
        transform: rotate(180deg);
        margin-top: -$measure--medium11 - $measure--quarter;
    }

    .pathway {
        .pathway__cards {
            display: grid;
            background: $color-purewhite;
            grid-template-columns: repeat(3, 1fr);
            max-width: unset;
            grid-column-gap: $measure--small2;
            background: $color-purewhite;
            position: relative;
            z-index: z-index('above');
            @include media('<tablet') {
                display: block;
            }
        }

        .card--pathway {
            list-style-type: none;
            opacity: 0;
            transition: opacity $transition-duration--longer3;
            margin-top: 0;
            z-index: z-index('above');
            @include box-shadow();
            &.in-view {
                opacity: 1;
            }
            .card--pathway__anchor {
                display: block;
                background-image: none;
                border: 0;
                &:focus,
                &:active {
                    border: none;
                    outline-style: $border-style-solid;
                    background: transparent;
                }
                &:hover {
                    @include media('hover') {
                        border: none;
                        .card--pathway__img {
                            transform: scale(1.1);
                        }
                    }
                }
            }
        }

        .wrap--icon {
            display: flex;
            margin-bottom: $measure--threequarters;
            &:after {
                background-color: $color-brand-primary;
                content: '';
                height: $measure--quarter;
                margin: $measure--small1 0 0 $measure--small2;
                width: 100%;
            }

            .wrap--icon__img {
                height: $measure--medium1;
            }
        }

        .wrap--card--pathway__img {
            overflow: hidden;
            height: 230px;
            .card--pathway__img {
                transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
            }
            & + .card--pathway__txt {
                padding-top: $measure--half;
            }
        }

        .card--pathway__txt {
            padding: $measure--basic;
            color: initial;
        }

        .card--pathway__heading {
            font-weight: $font-weight-bold;
            display: inline;
            border-bottom: $border-width-thick $border-style-solid
                $color-nearblack;
        }

        .card--pathway__desc {
            font-weight: $font-weight-normal;
        }
    }
}
