@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes scaleIn {
    0% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}

.home__social {
    grid-column: 1/-1;
    position: relative;

    @include media('<mobile') {
        .social__img {
            flex-basis: calc(100% / 2);
            max-width: calc(100% / 2);
        }
    }
    @include media('>=mobile', '<tablet') {
        .social__img {
            flex-basis: calc(100% / 3);
            max-width: calc(100% / 3);
        }
    }
    @include media('>=tablet') {
        .box {
            margin-left: $measure--medium7;
            width: $max-line-length;
            transform: translate(10%, -30%);
            top: 30%;
        }
        .box__border {
            border-left: 14px $border-style-solid transparent;
        }
        .box__content {
            margin: 0 $measure--small4 0 $measure--medium3;
        }
        .social__img {
            flex-basis: calc(100% / 5);
            object-fit: cover;
            width: 20%;
        }
    }
    @include media('>=desktop') {
        .box__border {
            border-left: 16px $border-style-solid transparent;
        }
    }
    @include media('>=1080p') {
        .box__border {
            border-left: 18px $border-style-solid transparent;
        }
    }
    @include media('>=4K') {
        .box__border {
            border-left: 36px $border-style-solid transparent;
        }
    }
    @include media('>=8K') {
        .box__border {
            border-left: 72px $border-style-solid transparent;
        }
    }

    .row {
        display: flex;
        overflow: hidden;
    }

    .social__img {
        &.in-view {
            animation: fadeIn $transition-duration--longer1,
                scaleIn $transition-function $transition-duration--longer5;
        }
        &:not(.in-view) {
            opacity: 0;
        }
    }

    .box {
        position: absolute;
        background: $color-purewhite;
        padding: $measure--small4 $measure--basic;
        margin: $measure--small4;
        z-index: z-index('above');
        &.in-view {
            animation: fadeIn $transition-duration--longer2;
        }
        &:not(.in-view) {
            opacity: 0;
        }
    }
    .box__pre-strap {
        text-transform: uppercase;
        display: flex;
        font-weight: $font-weight-bold;
        span {
            display: block;
            &.text {
                color: $color-brand-primary;
                @include font-family('heading');
                @include heading-font-features();
            }
            &.line {
                flex: 1;
                border-top: $border-width-hairline $border-style-solid
                    $color-gray6;
                margin: auto 0 auto $measure--basic;
            }
        }
    }
    .box__content__body {
        border-bottom: $border-width-hairline $border-style-solid solid
            $color-gray6;
        padding-bottom: $measure--basic;
        margin-bottom: $measure--basic;
    }
    .box__post-strap {
        .icon__wrapper {
            background-color: $color-brand-primary;
            border-radius: $measure--basic;
            color: $color-purewhite;
            display: inline-flex;
            height: $measure--small4;
            margin-bottom: $measure--half;
            width: $measure--small4;
            margin-right: $measure--half;
            .icon {
                margin: auto;
                &.fa-instagram {
                    &:before {
                        @include gammatype();
                    }
                }
            }
        }
        a.anchor-discreet {
            color: $color-brand-primary;
            border-bottom-color: $color-brand-primary;
            font-weight: $font-weight-medium;
            background-image: none;
            &:hover {
                &::before {
                    opacity: 0;
                    transition: none;
                }
            }
        }
    }

    // Create double grey triangle on left border
    .box__border {
        position: absolute;
        top: 0;
        bottom: 0;
        left: -1px; // To align with box's left border
        right: 0;
        background: transparent;
        width: 0;
        border-image: url(https://www.city.ac.uk/__data/assets/file/0015/710241/community.svg)
            65 round;
        filter: invert(67%) sepia(1%) saturate(0%) hue-rotate(151deg)
            brightness(98%) contrast(85%);
    }

    // Position second border to avoid overlap
    .box__border--two {
        left: $measure--threequarters;
    }
}
